.winter-section {
  padding-top: 120px;
  padding-bottom: 120px;
}

.js-snowboards-list {
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: -48px;
  margin-right: -22px;
  display: flex;
}

.snowboard-item {
  width: 296px;
  margin-bottom: 48px;
  margin-right: 22px;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.snowboard-des {
  flex-direction: column;
